import { Component } from "@angular/core";
import { NavController, NavParams } from "ionic-angular";
import { SingleChatComponent } from "../singleChat/single-chat";
import { Socket } from "ng-socket-io";
import { Observable } from "rxjs/Observable";
import { HttpClient } from "@angular/common/http";
import { AppSettings } from "../../../common/constant";
import { reorderArray } from "ionic-angular";
declare var jquery: any;
declare var $: any;
/**
 * Generated class for the ChatPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
  selector: "page-chat",
  templateUrl: "chat.html"
})
export class ChatPage {
  singleChatPage: any = SingleChatComponent;
  shouldShowCancel = true;
  userList = [];
  backupUserList = [];
  selectedUser: any;
  myInput: string = "";
  userLogin = "Welcome " + localStorage.getItem("name");
  username = localStorage.getItem("name");
  selectUser(user) {
    this.selectedUser = user;
    $("#" + user.userID).hide();
  }

  // notification
  public showNotification = false;
  showicon: boolean = false;
  notifyUser(userID) {
    console.log("notifyUser : ", userID + " :: " + this.showNotification);

    this.showicon = true;
    this.showNotification = true;
    if (this.showNotification) {
      if (document.getElementById(userID) != null)
        document.getElementById(userID).style.display = 'block';
    } else {
      if (document.getElementById(userID) != null)
        document.getElementById(userID).style.display = 'none';
    }
  }

  /* toggle(userID) {
     this.showicon = true;
     console.log(this.showNotification + " Msg Notification : ", userID);
     this.showNotification = !this.showNotification;
     //$("#" + userID).show();
     document.getElementById(userID).style.display = 'block'
     console.log(this.showNotification + " Msg Notification : ", userID);
     console.log(this.showicon, "  showicon : ");
     if (this.showNotification) {
       document.getElementById(userID).style.display = 'block'
     } else {
       document.getElementById(userID).style.display = 'none'
     }
 
   }*/


  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public socket: Socket,
    public http: HttpClient
  ) {
    this.username = localStorage.getItem("name");
    this.userList = [];
    this.socket.connect();
    console.log("this.socketId : ", this.socket.ioSocket.id);
    this.registerUser(
      AppSettings.userName,
      AppSettings.userEmail,
      this.socket,
      this.http
    );
    this.getUserList();
    this.getUsers().subscribe(users => {
      console.log("users ioSocket.id ... ", this.socket.ioSocket.id);
      this.filterUserList(users);
    });
  }

  reorderItems(indexes) {
    this.userList = reorderArray(this.userList, indexes);
  }

  filterUserList(users) {
    console.log("before filter userList : ", users);
    this.backupUserList = this.userList = users.filter(
      item => item.email != AppSettings.userEmail
    );
    console.log(" afetr filter userList : ", this.userList);
  }

  ionViewDidLoad() {
    console.log("ionViewDidLoad ChatPage");
  }

  registerUser(userName, userEmail, socket, http) {
    setTimeout(function () {
      console.log(
        "this.socket.ioSocket.id with timeout @registration : ",
        socket.ioSocket.id
      );
      http
        .post(AppSettings.url + "register", {
          dName: userName,
          email: userEmail,
          userID: socket.ioSocket.id
        })
        .subscribe(
        data => {
          console.log("registerUser : ", data);
        },
        error => {
          console.log("registerUser error : ", error);
        } // error path
        );
    }, 2000);
  }

  getUserList() {
    this.http.get(AppSettings.url + "getUserList").subscribe(
      data => {
        console.log("userList : ", data);
        this.filterUserList(data);
      },
      error => {
        console.log("getUserList error : ", error);
      } // error path
    );
  }

  getUsers() {
    let observable = new Observable(observer => {
      this.socket.on("userList", data => {
        console.log("observable data : ", data, observer);
        observer.next(data);
      });
    });
    return observable;
  }

  onInput(ev: any) {
    let val = ev.target.value;
    if (val && val.trim() && val.trim() != "") {
      this.userList = this.userList.filter(i => {
        return i.dName.toLowerCase().indexOf(val.toLowerCase()) > -1;
      });
    }
  }

  onCancel() {
    this.userList = this.backupUserList;
  }
}



