import { Component } from "@angular/core";
import { EmojiList } from "../singleChat/emojiList";
import {
  PopoverController,
  ViewController,
  NavParams,
  NavController,
  IonicPage
} from "ionic-angular";
import { ChatProvider } from "../chatService";
/**
 * Generated class for the SingleChatComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
// @IonicPage()
@Component({
  selector: "emoji-view",
  templateUrl: "emoji.html"
})
export class EmojiComponent {
  emojiList: Array<string>;
  selected: String[] = [];
  constructor(
    public popoverCtrl: PopoverController,
    public viewCtrl: ViewController,
    public navCtrl: NavController,
    public navParams: NavParams,
    public chatPro: ChatProvider
  ) {
    this.emojiList = EmojiList.emojiList;
  }
  selectedEmo(emo) {
    this.chatPro.changeEmoji(emo);
  }
}



