import { NgModule } from "@angular/core";
import { IonicPageModule } from "ionic-angular";
import { ChatPage } from "./mainChat/chat";
import { SingleChatComponent } from "./singleChat/single-chat";
import { SocketIoModule, SocketIoConfig } from "ng-socket-io";
import { AppSettings } from "../../common/constant";
import { AngularIndexedDB } from "angular2-indexeddb";
import { Ng2FileDropModule } from 'ng2-file-drop';
import { Ng2EmojiModule } from "ng2-emoji";
import { EmojiList } from "./singleChat/emojiList";
import { ChatProvider } from "./chatService";
import { EmojiComponent } from './emoji/emoji';

const config: SocketIoConfig = { url: AppSettings.url, options: {} };

@NgModule({
  declarations: [SingleChatComponent, ChatPage, EmojiComponent],
  exports: [ChatPage, SingleChatComponent, EmojiComponent],
  entryComponents: [SingleChatComponent, EmojiComponent],
  imports: [IonicPageModule.forChild(ChatPage), SocketIoModule.forRoot(config), Ng2FileDropModule, Ng2EmojiModule.forRoot()
  ],
  providers: [EmojiList, ChatProvider]
})
export class ChatPageModule { }



