import { Component, Input, ViewChild } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Socket } from "ng-socket-io";
import { AppSettings } from "../../../common/constant";
import { AngularIndexedDB } from "angular2-indexeddb";
import { Ng2FileDropModule, Ng2FileDropAcceptedFile, Ng2FileDropRejectedFile } from 'ng2-file-drop';
import { Observable } from "rxjs/Rx"
import { ChatPage } from '../mainChat/chat';
import { Content, LoadingController, PopoverController } from 'ionic-angular';
import { EmojiComponent } from "../emoji/emoji";
import { ChatProvider } from "../chatService";

declare var jquery: any;
declare var $: any;
/**
 * Generated class for the SingleChatComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: "single-chat",
  templateUrl: "single-chat.html"
})
export class SingleChatComponent {

  /*  @ViewChild(Content) content: Content;
  */
  textMsg: string = "";
  pairId = "";
  messages = []; // {"userID":"wferwvrewv", "pairID":"sadvdvv", "chat":"sdfevfv"}
  pairUser: any = {};
  user: any = {};
  db: AngularIndexedDB;
  audio = new Audio("../../../assets/sound/ping.mp3");
  isVisible: boolean = true;

  @Input("selectedUser")
  set selectedUser(data) {
    console.log("selected user : ", data);
    if (data) {
      this.pairId = data.userID;
      this.pairUser = data;
      this.getUserMsg(data);
      this.scrollToBottom()
    }
  }
  loading = this.loadingCtrl.create({
    spinner: 'hide',
    content: `
      <div class="custom-spinner-container">
        <div class="custom-spinner-box"></div>
      </div>`,
  });
  chatPage = new ChatPage(null, null, this.socket, this.http);
  headers: Headers;

  constructor(public loadingCtrl: LoadingController, public popoverCtrl: PopoverController,
    private http: HttpClient, private socket: Socket, public chatPro: ChatProvider) {
    this.headers = new Headers();
    this.headers.set('Content-Type', 'multipart/form-data');
    this.openDB();
    this.user = AppSettings;
    AppSettings.userEmail = localStorage.getItem("email");
    AppSettings.userName = localStorage.getItem("name");
    this.user = AppSettings;
    this.socket.on("newMsg", data => {
      console.log("receieve msg data : ", data);

      //notify
      if (data.userID != undefined) {
        if (data.userID != this.pairId) {
          this.chatPage.notifyUser(data.userID);
        }
      }
      console.log('this.isVisible : ', this.isVisible);
      this.addMessage(data);
      this.vis(function (e) { if (e.target.visibilityState == "hidden") { this.audio.play(); } });

      /*     if (this.isVisible) {
             console.log('inside audio play inside audio play inside audio play');
             this.audio.play();
           }*/
      if (data.email != this.pairUser.email) {
        this.audio.play();
      }
    });

    this.socket.on("download", this.receiveFile);

    /*  this.vis(function (e) {
        if (e.target.visibilityState == 'hidden') {
          this.isVisible = false;
        } else {
          this.isVisible = true;
        }
      });*/
    setTimeout(() => {
      this.chatPro.ObsEmoji.subscribe(emoji => {
        console.log("clicked emoji : ", emoji);
        if (emoji.toString().trim().length > 0) {
          this.textMsg += " :" + emoji + ": ";
        }
      });
    }, 1000);
  }

  vis = (function () {
    var stateKey, eventKey, keys = {
      hidden: "visibilitychange",
      webkitHidden: "webkitvisibilitychange",
      mozHidden: "mozvisibilitychange",
      msHidden: "msvisibilitychange"
    };
    for (stateKey in keys) {
      if (stateKey in document) {
        eventKey = keys[stateKey];
        break;
      }
    }
    return function (c) {
      if (c) document.addEventListener(eventKey, c);
      return !document[stateKey];
    }
  })();

  keyPress(ee) {
    if (ee.keyCode === 13 && !ee.shiftKey) {
      console.log("Enter button pressed...", ee);
      console.log(this.textMsg);
      this.sendMessage();
    }
  }

  openDB() {
    if (!("indexedDB" in window)) {
      console.log("This browser doesn't support IndexedDB");
      return;
    }
    this.db = new AngularIndexedDB("chatDB", 1);
    this.db.openDatabase(1, evt => {
      console.log("evt : ", evt);
      if (!evt.target.result.objectStoreNames.contains("people")) {
        let objectStore = evt.currentTarget.result.createObjectStore("people", {
          keyPath: "id",
          autoIncrement: true
        });
        objectStore.createIndex("name", "name", { unique: false });
        objectStore.createIndex("email", "email", { unique: true });
      }
    });
  }

  addMsgToDb(data, msgArr, pairUser) {
    console.log("addMsgToDb : ", data);
    var email = "",
      name = "";


    if (data.userID == this.socket.ioSocket.id) {
      console.log("Message from paiered user ", data, pairUser)
      email = pairUser.email;
      name = pairUser.dName;
    } else {
      //Message received
      console.log("Message from UP user ", data)
      email = data.email;
      name = data.dName;
    }

    this.db.getByIndex("people", "email", email).then(
      person => {
        console.log("user already exist in db=> data ");
        var arr = [];
        if (person) {
          console.log("got data for person : ", person);
          arr = person.message;
          arr.push(data);
        } else {
          arr.push(data);
        }
        this.db
          .update("people", {
            id: email,
            name: name,
            email: email,
            message: arr
          })
          .then(
          updated => {
            console.log("msg updated in db : ", updated);
          },
          error => {
            console.log("error while update in db : ", error);
          }
          );
      },
      error => {
        console.log("this user does not exist in db : ", error);
        this.db
          .add("people", {
            name: data.dName,
            email: data.email,
            message: [data]
          })
          .then(success => {
            console.log("message added in db : ", success);
          });
      }
    );
  }

  getUserMsg(pairUser) {
    console.log("user specidifc data : ", pairUser);
    this.db.getByIndex("people", "email", pairUser.email).then(
      person => {
        console.log("from db user specific data : ", person);
        if (person) this.messages = person.message;
        else this.messages = [];
      },
      error => {
        console.log("error user specific data : ", error);
      }
    );
  }

  sendMessage() {
    if (this.textMsg.trim().length == 0) {
      return;
    }
    this.http
      .post(AppSettings.url + "chats", {
        userID: this.socket.ioSocket.id,
        dName: AppSettings.userName,
        email: AppSettings.userEmail,
        pairID: this.pairId,
        chat: this.textMsg
      })
      .subscribe(
      data => {
        // console.log("message sent : ", data);
        data.__proto__.pairUserEmail = this.pairUser.email;
        console.log("data.__proto__.pairUserEmail : ", data.pairUserEmail);
        this.addMessage(data);
        this.textMsg = "";
      },
      error => {
        console.log("message sent error : ", error);
      }
      );
  }



  addMessage(data) {
    if (data.file) {
      data.file = {};
    }
    // console.log("this.pairUser : ", this.pairUser, data);
    //console.log("new msg : ", data);
    if (
      this.pairUser.email == data.email ||
      AppSettings.userEmail == data.email
    ) {
      this.messages.push(data);
      this.addMsgToDb(data, this.messages, this.pairUser);
    } else {

      this.addMsgToDb(data, this.messages, this.pairUser);
    }
    this.scrollToBottom();
  }

  scrollToBottom() {
    setTimeout(() => {
      var a = document.getElementsByClassName('scroll-content')
      var b = a[2];
      b.scrollTop = b.scrollHeight + 1;
    }, 100);
  }

  // code for drag & drop

  // File being dragged has moved into the drop region
  private dragFileOverStart() {
    console.log("dragFileOverStart");
    /*    document.getElementsByClassName('custom-component-drop-zone')[0].style.position = "absloute !important";
    */    // document.getElementsByClassName('content content-md')[3].style;
    document.getElementsByTagName('ion-content')[3].childNodes[1].style.display = 'none';
    /* b[0].style.display = 'none'*/
  }

  // File being dragged has moved out of the drop region
  private dragFileOverEnd() {
    console.log("dragFileOverEnd");
    /*    document.getElementsByClassName('custom-component-drop-zone')[0].style.position = "initial";
    */
  }

  // File being dragged has been dropped and is valid
  public dragFileAccepted(acceptedFile: Ng2FileDropAcceptedFile) {
    console.log("dragFileAccepted acceptedFile");
    // console.log("dragFileAccepted", acceptedFile.file);
    document.getElementsByTagName('ion-content')[3].childNodes[1].style.display = 'block';

    var file = acceptedFile.file;
    console.log("File name: " + file.name);
    /*var fileupoload = {
      userID: this.socket.ioSocket.id,
      pairID: this.pairId,
      email: AppSettings.userEmail,
      dName: AppSettings.userName,
      file: file,
      filename: file.name
    };
    this.socket.emit("fileUpload", fileupoload);*/
    /*    for(var pair of formData.entries()) {
       console.log(pair[0]+ ', '+ pair[1]); 
    }*/
    this.createform(file);
  }
  sendFile(e) {
    /*    console.log("File name: ", e);
    
        console.log("File name: " + file.name);
        var fileupoload = {
          userID: this.socket.ioSocket.id,
          pairID: this.pairId,
          file: file,
          filename: file.name
        };
        this.socket.emit("fileUpload", fileupoload);
    */
    console.log("File name: ", e);
    var file = e.target.files[0]
    this.createform(file);
  }

  receiveFile(data) {
    // var sampleBytes = new Int8Array(4096);
    //console.log("download", data);
    var saveByteArray = (function () {
      var a = document.createElement("a");
      document.body.appendChild(a);
      a.style = "display: none";
      return function (data, name) {
        var blob = new Blob(data, {
          type: "octet/stream"
        }),
          url = window.URL.createObjectURL(blob);
        a.href = url;
        a.download = name;
        a.click();
        window.URL.revokeObjectURL(url);
      };
    })();
    saveByteArray([data.file[0].buffer], data.filename);
  }
  createform(file) {
    var reqformData = new FormData();
    reqformData.append('userID', this.socket.ioSocket.id);
    reqformData.append('pairID', this.pairId);
    reqformData.append('file', file);
    reqformData.append('email', AppSettings.userEmail);
    reqformData.append('filename', file.name);
    this.postForm(reqformData)
  }

  postForm(form) {
    console.log("Calling postForm");
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'multipart/form-data'
      })
    }
    $(".loader").css("display", 'block');
    $("body").css("opacity", 0.5);
    $("body").css("pointer-events", 'none');
    return this.http.post(AppSettings.url + "upload", form).subscribe(
      data => {
        console.log("File uploadsed Sucessfully");
        $('body').css('opacity', 1);
        $("body").css("pointer-events", 'auto');
        $(".loader").css("display", 'none');

        /*        this.loading.dismiss();
        */
      },
      error => {
        console.log("message sent error : ", error);
      }
    );;
  }
  public handleError = (error: Response) => {
    // Do messaging and error handling here
    return Observable.throw(error)
  }
  // File being dragged has been dropped and has been rejected
  private dragFileRejected(rejectedFile: Ng2FileDropRejectedFile) {
    console.log("dragFileRejected");
  }



  presentLoadingDefault() {
    let loading = this.loadingCtrl.create({
      content: 'Please wait ... Uploading file to Server'
    });


    /* setTimeout(() => {
       loading.dismiss();
     }, 5000);*/
  }
  presentLoadingCustom() {
    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: `
      <div class="custom-spinner-container">
        <div class="custom-spinner-box">
            <div class="loader"></div>
        </div>
      </div>`,
    });
  }

  openPopover(myEvent) {
    console.log("popover open called...");
    let popover = this.popoverCtrl.create(EmojiComponent);
    popover.present({
      ev: myEvent
    });
    popover.onDidDismiss(data => {
      console.log(data);
      if (data != null) {
        console.log("data from pop : ", data);
      }
    });
  }

}



